import React from "react";
import { graphql,Link } from "gatsby";
import Img from "gatsby-image";
import Moment from "react-moment";
import Layout from "../components/layout";
import Markdown from 'react-markdown';
import { mdRenderLinkInNewTab,mdBuildImgRenderer } from '../util';

export const query = graphql`
  query ArticleQuery($slug: String!) {
    strapiArticle(slug: { eq: $slug }, status: { eq: "published" }) {
      strapiId
      title
      description
      content
      content_images {
        childImageSharp {
          original {
            src
          }
          fluid {
            ...GatsbyImageSharpFluid
          }
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
        base
      }
      publishedAt
      tags{
          tagName
          slug
      }
      image {
        publicURL
        childImageSharp {
          fixed {
            src
          }
        }
      }
      author {
        name
        picture {
          childImageSharp {
            fixed(width: 30, height: 30) {
              src
            }
          }
        }
      }
    }
  }
`;

const Article = ({ data }) => {
  const article = data.strapiArticle;
  const seo = {
    metaTitle: article.title,
    metaDescription: article.description,
    shareImage: article.image,
    article: true,
  };

  return (
    <Layout seo={seo} containerMaxWidth="1700"> {/* specify the maxWidth to expand the content div width on ultra wide screens */}
        {/* Title */}
        <h1 className="mt-4">{article.title}</h1>

        {/* Date/Time */}
        <p className="small text-secondary text-right">發佈於  <Moment format="YYYY/MM/DD HH:mm:ss" date={article.publishedAt}/> </p>

        <hr/>

        {/* Preview Image */}
        <div style={{display:'flex',flexDirection:'column', alignItems:'center'}}>
          <Img className="img-fluid rounded"
                    fixed={article.image.childImageSharp.fixed}
                    imgStyle={{ position: "static" }}
                  />
        </div>
        <hr/>

        {/* Post Content */}
        <div style={{lineHeight:2, fontSize:'1.5rem'}}>
          <Markdown source={article.content} escapeHtml={false} 
                    renderers={{ link: mdRenderLinkInNewTab, image:  mdBuildImgRenderer(article.content_images) }}/>
        </div>
        
        <hr/>

        {/* TBD - 留言 */}

    </Layout>
  );
};


export default Article;